import React from 'react'
import axios from "axios";
import { useEffect, useState } from "react";
import { useNavigate, useParams, Link } from "react-router-dom";
import AOS from 'aos';
import 'aos/dist/aos.css'; // Import the CSS

export default function Detail_Location() {
  AOS.init(); // Initialize AOS

  const navigate = useNavigate();

  const [locations, setlocations] = useState([]);

  const { id } = useParams();

  useEffect(() => {
    getLocation();
  }, []);
  const getLocation = async () => {
    try {
      const response = await axios.get(
        `https://thewateraccident.000webhostapp.com/location.php/${id}/?xCase=0`
      );
      setlocations(response.data);
    } catch (error) {
      console.error(error);
    }
  };
  return (
    <div className='body-header'>
      <section >
        <div className='container'>
          <div className='topPic fs-1 fw-bold text-center'>
            พื้นที่เสี่ยง {locations.namelocation}
          </div>
        </div>
        <div className='page-header w-100'>
          <div className='container'>
            สถานที่ {` > `} {locations.namelocation}
          </div>
        </div>
        <div className='container' >

          <div className={`bg accident-${locations.typeriskID} text-center p-3 my-3 p-md-5 my-md-5`} data-aos="fade-up">
            <div className="row mx-auto">
              <div className="col-4 m-auto"><img src="/img/warning.png" className="w-100" alt="" /></div>
              <div className="col-8 m-auto"><h1 className='text-white fw-bold '>พื้นที่{locations.typeriskName}</h1></div>
            </div>
          </div>
          <div data-aos="fade-up">
            กดเลือกรูปภาพเพื่อขยาย
          </div>
          {/* content */}
          <div className='w-100 bg-light text-center mb-3' data-aos="fade-up">

            <div className='detailheader fw-bold fs-4 py-2'>
              จุดวางอุปกรณ์
            </div>
            <img src={`https://thewateraccident.000webhostapp.com/${locations.img}`} className='w-100' alt="" />
          </div>
          {/* content */}
          <div className='w-100 bg-light text-center mb-3' data-aos="fade-up">
            <div className='detailheader fw-bold fs-4 py-2'>
              วิธีการใช้อุปกรณ์
            </div>
            <img src={`https://thewateraccident.000webhostapp.com/${locations.img}`} className='w-100' alt="" />
          </div>
          {/* content */}
          <div className='card w-100 bg-light text-center my-4' data-aos="fade-up">
            <div className='card-title detailheader fw-bold fs-4 mt-3'>
              การปฏิบัติตัวภายในพื้นที่
            </div>
            <div className="card-body">
              <ol>
                <li>หลีกเลี่ยงการเข้าใกล้แหล่งน้ำ</li>
                <li>ไม่ควรทำกิจกรรมในสถานที่แหล่งน้ำอันตราย</li>
                <li>ดูพยากรณ์อากาศของพื้นที่นั้นก่อนที่จะเข้าไปในสถานที่นั้น</li>
                <li>ควรปฏิบัติตามกฎของสถานที่เคร่งครัด</li>
              </ol>
            </div>
          </div>
          {/* content */}
          <div className='card w-100 bg-light text-center my-4' data-aos="fade-up">
            <div className='card-title detailheader fw-bold fs-4 mt-3'>
              การป้องกัน
            </div>
            <div className="card-body">
              <ol>
                <li>ศึกษาการใช้อุปกรณ์ในการช่วยเหลือป้องกันการเกิดอุบัติเหตุทางน้ำ</li>
                <li>เตรียมความพร้อมเรียนรู้การปฏิบัติตัวเพื่อไม่ให้เกิดอุบัติเหตุทางน้ำจากเว็บแอพพลิเคชั่นมาก่อนเข้าพื้นที่</li>
                <li>สังเกตจุดจัดเก็บอุปกรณ์</li>
                <li>สังเกตป้ายเตือนตามสถานที่ต่างๆ</li>
              </ol>
            </div>
          </div>

        </div>
      </section>
    </div>
  )
}
